@layer utilities {
  .border {
    border-style: var(--tw-border-style);
    border-width: 1px;
  }
  .antialiased {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}
:root {
  --background: #afe6e3;
  --foreground: #171717;
}
@media (prefers-color-scheme: light) {
  :root {
    --background: #131010;
    --foreground: #dbc2c2;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
}
@layer components {
  .background {
    background-color: #89b7d6ff;
  }
  .greenBG {
    background-color: #3b9b3bff;
  }
  .redBG {
    background-color: #a53737ff;
  }
  .blueBG {
    background-color: #3030b1ff;
  }
  .orangeBG {
    background-color: rgb(170, 89, 22);
  }
  .textColor {
    color: #333333;
  }
  .greenText {
    color: #008000;
  }
  .redText {
    color: #800000;
  }
  .blueText {
    color: #000080;
  }
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    max-width: 1400px;
    margin: 30px auto;
    padding: 20px;
    border-radius: 10px;
    gap: 0;
    background: rgb(141, 156, 214);
  }
  .textSection {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    width: 50%;
    height: 100%;
    margin: 0 5%;
    color: white;
  }
  .sideBySide {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    min-height: 500px;
    gap: 20px;
    padding: 20px 0px;
  }
  .locationContainer {
    margin: 20px 0px;
    padding: 20px;
    font-size: 32px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
  }
  .title {
    font-size: 32px;
    font-weight: bold;
    margin-top: 20px;
    margin-bottom: 50px;
  }
  .responsiveImage {
    width: 100%;
    max-width: 700px;
    height: auto;
    object-fit: cover;
    flex: 1;
  }
  .listItem {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin-bottom: 5px;
  }
  .bullet {
    width: 10px;
    margin-right: 10px;
    font-size: 20px;
    line-height: 28px;
  }
  .text {
    flex: 1;
    font-size: 20px;
  }
  .mapContainer {
    width: 50%;
    height: 500px;
    padding: 10px;
  }
  .announcement {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #3b9b3bff;
    color: white;
    margin-bottom: 20px;
    padding: 15px 30px;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }
  .registration {
    display: flex;
    flex-direction: column;
    gap: 12px;
    width: 100%;
    max-width: 640px;
    margin: 0 auto;
    padding: 12px;
    background: rgba(255,255,255,0.6);
    border-radius: 8px;
  }
  .registration input,
    .registration select,
    .registration textarea {
    padding: 10px 12px;
    font-size: 16px;
    border-radius: 6px;
    border: 1px solid #757070;
    width: 100%;
    box-sizing: border-box;
  }
  .registration textarea {
    min-height: 120px;
    resize: vertical;
  }
  .registration input[type="submit"] {
    background: #3b9b3b;
    color: #fff;
    padding: 10px 16px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-weight: 600;
  }
  .registration input:focus,
    .registration select:focus,
    .registration textarea:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(59,155,59,0.12);
    border-color: #3b9b3b;
  }
  .registration input.error,
    .registration select.error,
    .registration textarea.error {
    border-color: #a53737ff;
    box-shadow: none;
  }
  .registration input.error::placeholder,
    .registration textarea.error::placeholder,
    .registration select.error::placeholder {
    color: #a53737ff;
    opacity: 1;
  }
  @media (max-width: 768px) {
    .content {
      width: 95%;
    }
    .sideBySide {
      flex-direction: column;
      min-height: auto;
    }
    .title {
      font-size: 24px;
    }
    .responsiveImage {
      max-width: 100%;
    }
    .mapContainer {
      height: 400px;
      width: 100%;
    }
    .textSection {
      width: 90%;
    }
  }
}
@property --tw-border-style {
  syntax: "*";
  inherits: false;
  initial-value: solid;
}
@layer properties {
  @supports ((-webkit-hyphens: none) and (not (margin-trim: inline))) or ((-moz-orient: inline) and (not (color:rgb(from red r g b)))) {
    *, ::before, ::after, ::backdrop {
      --tw-border-style: solid;
    }
  }
}